@media screen and (min-width: 768px) {
  .font-flex {
    flex-direction: row;
    gap: 10px;
  }

  .font-size {
    color: #545454;
    text-align: center;
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  .card-team {
    width: 80%;
    height: 85%;
    border: 2px solid;
    border-radius: 5px;
    margin-bottom: 10px;
